<template>
  <v-container grid-list-xs>
    <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="date"
            label="Picker in menu"
            prepend-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
          ></v-text-field>
        </template>
      </v-menu>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/student/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Student</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-dialog
      v-model="confirmDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Confirm
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="confirmDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          Are you sure you want to add  <b>{{ practitionername }} </b><br>
          as a family member of <b>{{ familyHead["Practitioner.name.where(use='official').given"] }} {{ familyHead["Practitioner.name.where(use='official').family"] }}</b>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="addMember">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <b>Student: {{ practitionername }} - {{ practitionerregnum }}</b>
    <v-card>
      <v-card-title>
        Current Family Head
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col>
            Name: <b>{{ currentHead.name }}</b>
          </v-col>
          <v-col>
            Registration Number: <b>{{ currentHead.regNum }}</b>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <v-card>
      <v-card-title>Select Family Head Name</v-card-title>
      <v-card-text>
        <v-row>
          <v-col>
            <v-text-field
              label="Search Name"
              v-model="name" 
              outlined 
              dense
              @keyup.enter = "search"
            >
            </v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              label="Search Registration Number"
              v-model="regNum" 
              outlined 
              dense
              @keyup.enter = "search"
            >
            </v-text-field>
          </v-col>
          <v-col cols="2">
            <v-btn color="success" @click="search">Search</v-btn>
          </v-col>
        </v-row>
        <v-data-table
          style="cursor: pointer"
          :headers="headers"
          :items="results"
          item-key="id"
          :options.sync="options"
          :server-items-length="total"
          :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
          :loading="loading"
          class="elevation-1"
          @click:row="selected"
        ></v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      date: '',
      menu: false,
      overlay: false,
      confirmDialog: false,
      practitionername: '',
      practitionerregnum: '',
      familyHead: {},
      total: 0,
      name: '',
      regNum: '',
      headers: [{
        "text": "Surname",
        "value": "Practitioner.name.where(use='official').family"
      }, {
        "text": "First Name(s)",
        "value": "Practitioner.name.where(use='official').given"
      }, {
        "text": "Date of Birth",
        "value": "Practitioner.birthDate"
      }, {
        "text": "Gender",
        "value": "Practitioner.gender"
      }, {
        "text": "Registration Number",
        "value": "Practitioner.extension.where(url='http://ihris.org/fhir/StructureDefinition/registration-number').valueString"
      }],
      fields: [
        ["Surname", "Practitioner.name.where(use='official').family", null],
        ["First Name(s)", "Practitioner.name.where(use='official').given", null],
        ["Date of Birth", "Practitioner.birthDate", null],
        ["Gender", "Practitioner.gender", "http://ihris.org/fhir/CodeSystem/itsf-gender-codesystem"],
        ["Registration Number", "Practitioner.extension.where(url='http://ihris.org/fhir/StructureDefinition/registration-number').valueString", null]
      ],
      results: [],
      loading: false,
      update_again: { rerun: false, restart: false },
      options: { itemsPerPage: 10 },
      currentHead: {
        name: '',
        regNum: ''
      }
    }
  },
  watch: {
    options: {
      handler() {
        this.search();
      },
      deep: true
    }
  },
  methods: {
    selected: function(record) {
      this.familyHead = record
      this.confirmDialog = true
    },
    addMember() {
      this.overlay = true
      this.confirmDialog = false
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile&practitioner=Practitioner/" + this.queries.practitioner).then((response) => {
        response.json().then((entry) => {
          let head = {}
          let extension = [{
            url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
            valueReference: {
              reference: "Practitioner/" + this.queries.practitioner
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/family-head",
            valueReference: {
              reference: "Practitioner/" + this.familyHead.id
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/full-name",
            valueString: this.familyHead["Practitioner.name.where(use='official').given"] + " " + this.familyHead["Practitioner.name.where(use='official').family"]
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/registration-number",
            valueString: this.familyHead["Practitioner.extension.where(url='http://ihris.org/fhir/StructureDefinition/registration-number').valueString"]
          }]
          let method = ""
          let id = ""
          if(entry && entry.entry && entry.entry.length > 0) {
            method = "PUT"
            head = entry.entry[0].resource
            head.extension = extension
            id = "/" + head.id
          } else {
            method = "POST"
            head = {
              resourceType: "Basic",
              meta: {
                profile: ["http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile"]
              },
              extension: extension
            }
          }
          let opts = {
            method,
            headers: {
              "Content-Type": "application/fhir+json"
            },
            redirect: 'manual',
            body: JSON.stringify(head)
          }
          fetch("/fhir/Basic" + id, opts).then((response) => {
            this.overlay = false;
            if(response.status == 201 || response.status == 200) {
              this.getFamilyHead()
              this.$store.commit("setMessage", {
                type: "success",
                text: "Family member added",
              });
            } else {
              this.$store.commit("setMessage", {
                type: "error",
                text: "Failed to add family member."
              });
            }
          }).catch(() => {
            this.overlay = false;
            this.$store.commit("setMessage", {
              type: "error",
              text: "Failed to add family member."
            });
          })
        }).catch(() => {
          this.$store.commit("setMessage", {
            type: "error",
            text: "An error has occured."
          });
        })
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured."
        });
      })
    },
    getFamilyHead() {
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile&practitioner=Practitioner/" + this.queries.practitioner).then((response) => {
        response.json().then((entry) => {
          if(entry && entry.entry && entry.entry.length > 0) {
            let head = entry.entry[0].resource
            console.error(JSON.stringify(head, 0, 2));
            this.currentHead.name = head.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/full-name'
            }).valueString
            this.currentHead.regNum = head.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/registration-number'
            })?.valueString
          }
        })
      })
    },
    checkRerun() {
      if ( !this.loading && this.update_again.rerun ) {
        this.search( this.update_again.restart )
        this.update_again = { rerun: false, restart: false }
      }
    },
    search(restart) {
      let query = ""
      if(this.name) {
        query += "&name:contains=" + this.name
      }
      if(this.regNum) {
        query += "&regnum:contains=" + this.regNum
      }
      if ( this.loading ) {
        this.update_again.rerun = true
        this.update_again.restart = this.update_again.restart || restart
        return
      }
      this.loading = true;
      this.error_message = null;
      let url = "";
      if (restart) this.options.page = 1;
      if (this.options.page > 1) {
        if (this.options.page === this.prevPage - 1) {
          url = this.link.find(link => link.relation === "previous").url;
        } else if (this.options.page === this.prevPage + 1) {
          url = this.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace(/_getpages=[^&]*&*/, "").replace("/fhir?","/fhir/Practitioner?")
        url = url.substring(url.indexOf("/fhir/"));

        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
      }
      if (url === "") {
        let count = this.options.itemsPerPage || 10;
        let sort = "";
        for (let idx in this.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.options.sortBy[idx];
        }
        url =
          "/fhir/Practitioner?_count=" + count + "&_total=accurate"
          url += query
      }
      this.prevPage = this.options.page;
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.results = [];
          if (data.total > 0) {
            this.link = data.link;
            for (let entry of data.entry) {
              let result = { id: entry.resource.id };
              let ignore = false
              for (let field of this.fields) {
                let fieldDisplay = this.$fhirpath.evaluate( entry.resource, field[1] );
                result[field[1]] = await this.$fhirutils.lookup( fieldDisplay[0], field[2] )
                if(field[1] === "Practitioner.extension.where(url='http://ihris.org/fhir/StructureDefinition/registration-number').valueString" && result[field[1]] === this.practitionerregnum) {
                  ignore = true
                  break
                }
              }
              if(ignore) {
                continue
              }
              this.results.push(result);
            }
          }
          this.total = data.total;
          this.loading = false;
          this.checkRerun()
        }).catch(err => {
          this.loading = false;
          this.error_message = "Unable to load results.";
          this.checkRerun()
          console.log(err);
        });
      }).catch(err => {
        this.loading = false;
        this.error_message = "Unable to load results.";
        this.checkRerun()
        console.log(err);
      });
    }
  },
  created() {
    this.getFamilyHead()
    fetch("/fhir/Practitioner/" + this.queries.practitioner).then((response) => {
      response.json().then((pract) => {
        this.practitionername = ""
        this.practitionerregnum = pract.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/registration-number'
        })?.valueString
        let name = pract.name.find((nm) => {
          nm.use === "official"
        })
        if(!name && pract.name && pract.name.length > 0) {
          name = pract.name[0]
        }
        if(name) {
          if(name.given) {
            if(this.practitionername) {
              this.practitionername += " "
            }
            this.practitionername += name.given.join(" ")
          }
          if(name.family) {
            if(this.practitionername) {
              this.practitionername += " "
            }
            this.practitionername += name.family
          }
        }
      })
    })
  }
}
</script>